.skills-main {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 35%;

}

.image {
  width: 45vmin;
  width: 25vmin;
  object-fit: cover;
  object-position: center;
  transition: filter 0.5s ease-in-out, background-color 0.5s ease-in-out;
}

.project-1,
.project-2
{
  display: flex;
  flex-direction: column;
  align-items: center;

}

.description {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
 
}

.no-gap {
 line-height: 1.5;
}



.p1,
.p2
{
  text-align: center;
  border: 1px solid rgba(245, 245, 220, 0.185);
  margin-top: 5%;
  transition: all 0.3s ease-in;
}

.image:hover {
  filter: brightness(150%);
  background-color: rgba(255, 190, 129, 0.7);
}


@media only screen and (max-width: 1200px) {
  .skills-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    gap: 4rem;
  }

}